<template>
    <aside id="sidebar"
           class="sidebar"
           :class="{active: $store.getters.isMenuOpen}"
    >
        <div class="sidebar__top">
            <div class="sidebar__burger"
                 @click="toggleSidebar">
                <img :src="burger[$store.getters.getThemeName] || burger.default" alt="">
            </div>
            <router-link to="/">
                <div class="sidebar__logo">
                    <img :src="$store.getters.getLogoSidebar" alt="">
                </div>
            </router-link>
        </div>
        <div class="sidebar__view">

            <ul class="sidebar__list sidebar__list-first">
                <li v-for="item in sidebarData.items" class="sidebar__item"
                    :style="{marginBottom : + item.separator + 'px' }">
                    <router-link
                            :to="item.link"
                            class="sidebar__link"
                            v-if="item.link"
                    >
                        <img :src="item.img" class="svg" alt="">
                        <span class="title">{{item.title}}</span>
                    </router-link>
                    <p v-else class="sidebar__link" @click="toggleList">
                        <img :src="item.img" class="svg" alt="">
                        <span class="title">{{item.title}}</span>
                    </p>
                    <ul class="sidebar__list sidebar__list-deep" v-if="+item.group_l">
                        <li class="sidebar__item" v-if="!item.children">
                            <img :src="item.img" @click.stop class="svg" alt="">
                            <span class="title">{{item.title}}</span>
                        </li>
                        <template v-for="child in item.children">
                            <MenuItem
                                    class="item"
                                    :model="child"
                                    :parameters="sidebarData.menu_parameters"
                            >
                            </MenuItem>
                        </template>
                    </ul>
                </li>
            </ul>

        </div>

    </aside>
</template>
<script>
    import $ from 'jquery';
    import swipe from 'jquery-touchswipe/jquery.touchSwipe';
    import MenuItem from '../components/MenuItem.vue';

    export default {
        data() {
            return {
                burger: {'default': '/img/menu.png', 'rb': '/img/menu2.png'},
            }
        },
        props: ['sidebarData'],
        methods: {
            /**
             * Закрытие всех папок
             * @return {boolean} foldersOpen Папка открыта
             */
            closeAllFolders() {
                let foldersOpen = false;
                let folders = document.querySelectorAll('.sidebar__list-deep');
                Array.from($('.sidebar__link.active')).forEach(e => {
                    $(e).removeClass('active')
                })
                Array.from(folders).forEach(folder => {
                    if (folder.style.display === 'block') {
                        foldersOpen = true;
                        $(folder).slideUp();
                    }
                })
                return foldersOpen;
            },
            /**
             * Переключение типа Sidebar
             */
            toggleSidebar() {
                let foldersOpen = this.closeAllFolders();
                if (foldersOpen)
                    setTimeout(() => {
                        this.$store.commit('toggleSidebar');
                    }, 400);
                else this.$store.commit('toggleSidebar');
            },
            /**
             * Закрытие соседних папок
             * @param {element} item Папка открыта
             */
            closeSiblings(item) {
                let itemSiblings = item.siblings();
                Array.from(itemSiblings).forEach(sibling => {
                    if (sibling === item) return;
                    let lists = sibling.querySelectorAll('.sidebar__list');

                    Array.from(lists).forEach(list => {
                        if (list.style.display !== 'block') return;
                        $(list).slideUp();

                        $(list).prev().removeClass('active');
                    })
                })

            },
            /**
             * Открытие папки
             * @param {event} e ивент клика на пункт меню с вложеной папкой
             */
            toggleList(e) {
                let elem = e.target;
                let sidebarList = $(elem.closest('.sidebar__item').querySelector('.sidebar__list'));
                let sidebarItem = $(elem.closest('.sidebar__item'));
                if (!this.$store.getters.isMenuOpen)
                    this.closeSiblings(sidebarItem);
                if (!this.$store.getters.isMenuOpen) {
                    let heightHtml = document.documentElement.clientHeight;
                    let elemTop = e.target.getBoundingClientRect().top;
                    let sidebarHeight = sidebarList.height();
                    heightHtml <= elemTop + sidebarHeight ?
                        $(sidebarList).addClass('bottom') : false
                }
                new this.$toggler(sidebarList[0]).toggle();
                $(sidebarList).prev().toggleClass('active')


            },
        },
        watch: {
            // При переходе на другой роут закрывать все папки
            $bRoute(to, from) {
                if (window.innerWidth > 768 && !this.$store.getters.isMenuOpen) {
                    this.closeAllFolders()
                } else if (window.innerWidth < 768)
                    this.$store.state.menu_open = false;
            },


        },
        updated() {
            this.imgtosvg();
        },
        mounted() {
            let self = this;
            setTimeout(function () {
                // Конвертация img.svg в SVG
                self.imgtosvg();

                // Свайпы для мобильной версии
                if (window.innerWidth < 768) {
                    // Открытие меню
                    $("#main").swipe({
                        allowPageScroll: "auto",
                        excludedElements: "label, button, input, select, textarea, .noSwipe",
                        swipe: function (event, direction, distance, duration, fingerCount, fingerData) {
                            if (fingerData[0].start.x < 50 && direction === 'right') self.toggleSidebar();
                        }
                    });
                    // Закрытие меню
                    $("#sidebar").swipe({
                        allowPageScroll: "auto",
                        swipe: function (event, direction, distance, duration, fingerCount, fingerData) {
                            if (direction === 'left') self.toggleSidebar();
                        }
                    });
                }
            }, 200)
            // Если меню в закрытом режими, то при клике на страницу скрывать dropdown
            document.addEventListener('click', function (e) {
                if (!self.$store.state.menu_open && e.target.closest("#sidebar") == null)
                    self.closeAllFolders()
            })
        },

        components: {
            MenuItem
        }
    }
</script>

