<template>
    <div id="app">
	    <transition-group name="fade" mode="in-out">
		    <div class="spinner" v-show="isLoading" key="spinner">
			    <atom-spinner
				    :animation-duration="1000"
				    color="var(--accent)"
			    />
		    </div>
        </transition-group>
       <Main v-if="this.$store.getters.isLoggedIn && refreshMain"></Main>
       <template v-else>
        <Verify @verificationSubmitted="reset_password_required = false"
                @verificationFailed="reset_password_required = false"

                v-if="reset_password_required" type="reset_password"
            ></Verify>
            <Auth v-else></Auth>
        </template>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    import Main from './components/Main';
    import Auth from './components/Auth';
    import Verify from './components/Verify';
    import {AtomSpinner} from 'epic-spinners';
    import {EventBus} from "./main";

    export default {
        name: 'auth',
        components: {
            Main,
            Auth,
            Verify,
            AtomSpinner
        },
        data: function () {
            return {
                refreshMain: true,
                isLoading:     true,
                resetPasswordModal: {
                    modal: {
                        ipost: "/consumer/changePassword/confirm",
                        inputs: [
                            // {
                            //     model: "oldPassword",
                            //     title: "Старый пароль",
                            //     type: "password",
                            //     validation: "required|min:6|max:20",
                            // },
                            {
                                model: "newPassword",
                                title: "Новый пароль",
                                type: "password",
                                ref: "newpass",
                                validation: "required|min:6|max:20|confirmed:repeatpass",
                                returnfield: "first_name"
                            },
                            {
                                model: "repeatPassword",
                                title: "Повторить пароль",
                                type: "password",
                                ref: "repeatpass",
                                validation: "required|min:6|max:20|"
                            }
                        ],
                        actions: [
                            {
                                title: "Ok",
                                code: "await.ModalRequest,showtoast,ReturnRes,ifResetModal,hideModal",
                                class: "btn btn-green",
                                img: ""
                            },
                            {
                                title: "Отмена",
                                code: "resetModal,focehideModal",
                                class: "btn btn-grey"
                            },
                            {
                                title: "Сбросить", "code": "resetModal", "class": "btn btn-grey"
                            }
                        ],
                    }
                },
                reset_password_required: false
            }
        },
        async created() {
            await this.$store.dispatch('checkTheme');
            EventBus.$on('changeTheme', () => {
                this.isLoading = true;
                setTimeout(()=>{this.isLoading = false},1000)
            })
            if (this.$bRoute.path === '/verify') {
                try {
                    let res = await this.$http.post('/consumer/verify/' + this.$bRoute.query.token);
                    if (res.data.token) {
                        this.$store.commit('loginUser', res.data.token);
                        this.$store.commit('addUser1cToken', res.data.token_1c);
                        this.$bRouter.replace(this.$store.getters.home_url);
                    } else {
                        await this.$store.dispatch('showToast', {
                            template: res.data.template,
                            title: res.data.title,
                            message: res.data.message,
                            position: res.data.position,
                        })
                        if (res.data.reset_password) {
                            //если пришел флаг на изменение пароля - вызываем модальное окно
                            this.$store.commit('addUserResetToken', res.data.reset_token);
                            this.reset_password_required = true;
                            // this.resetPassword(response.data.reset_token);
                        }
                    }
                } catch (error) {
                    await this.$store.dispatch('showToast', {
                        template: 'error',
                        title: error.response.status,
                        message: error.response.data.message,
                        position: error.response.data.position,
                    })
                }


            }

        },
        computed: {},
        watch: {},
        methods: {
            /**
             * вызов модального окна для изменения пароля
             * @param {string} reset_token - токен для замены пароля с почты
             * @returns {Promise<void>}
             */
            // async resetPassword(reset_token) {
            //     this.resetPasswordModal.modal.ipost += '/' + reset_token;
            //     await this.$refs.dmodal_app.showmodal(this.resetPasswordModal);
            //     let result = this.getModalResponse;
            //     this.$store.commit('setModalResponse', '');
            // }
        },
        async mounted() {
            this.$nextTick(e => {
                setTimeout(e=>{this.isLoading = false;},1000)
            })
        }
    };
</script>

