import themes from "../../themes";
import {EventBus} from '../../main'
const state = {
    themeName: process.env.VUE_APP_BASE_THEME,
    themes: themes.themes
};

const actions  = {
    changeTheme(state, theme) {
        EventBus.$emit('changeTheme');
        setTimeout(e=>{
            state.commit('changeTheme',theme);
            state.dispatch('checkTheme');
            document.location.reload(true);
        },200)
    },
    checkTheme(state){
        let html = document.documentElement;
        html.className = state.getters.getTheme.code;
        for (let [key, value] of Object.entries(state.getters.getTheme.variables)) {
            let propertyName = '--' + key;
            html.style.setProperty(propertyName,value);
        }
        localStorage.setItem('theme',state.getters.getThemeName);
    }
}

const mutations = {
    changeTheme(state, theme) {
        state.themeName = theme.code;
    },
};

const getters = {
    getTheme: state => {
        return state.themes[state.themeName];
    },
    getLogoSidebar: state =>{
        return state.themes[state.themeName].settings.logoSidebar;
    },
    getLogo: state =>{
        return state.themes[state.themeName].settings.logo;
    },
    getThemeName: state => {
        return state.themeName;
    },
    getThemesStore : state => {
        return state.themes;
    }
}

export default {
    state,
    mutations,
    getters,
    actions
}
