export const saveFile = {
    methods: {
        /**
         * сохраняет файл, переданный в base64 с сервера
         * @param {object} res - обьект с файлом
         */
        saveFile(res) {
            // debugger;
            var ua = window.navigator.userAgent;
            var msie = ua.indexOf('Trident');

            const fileName = res.name + '.' + res.extension;

            if (msie > -1) {
                const byteCharacters = atob(res.file);
                const byteNumbers = new Array(byteCharacters.length);
                for (let i = 0; i < byteCharacters.length; i++) {
                    byteNumbers[i] = byteCharacters.charCodeAt(i);
                }
                const byteArray = new Uint8Array(byteNumbers);

                var blobObject = new Blob([byteArray]);

                window.navigator.msSaveBlob(blobObject, fileName);

            }
            else {
                const linkSource = res.mime + ',' + res.file;
                const downloadLink = document.createElement("a");
                downloadLink.href = linkSource;
                downloadLink.download = fileName;
                downloadLink.click();
            }


        }
    }
};