<script>
    import {mapGetters} from 'vuex';
    import VueRecaptcha from 'vue-recaptcha';
    import axios from 'axios';

    export default {
        name: "Login",
        data() {
            return {
                email: '',
                password: '',
                confirmPassword: '',
                firstName: '',
                lastName: '',
                phone: '',
                captcha: '',
                loginName: '',
                error: '',
                captchaImg: '',
                result: {
                    status: '',
                    msg: '',
                },
                type: 'login', // login,register,changePassword
                reCaptchaToken: null
            }
        },
        watch: {
            type(newD) {
                this.email = '';
                this.phone = '';
                this.captcha = '';
                this.loginName = '';
                this.lastName = '';
                this.firstName = '';
                this.confirmPassword = '';
                this.password = '';
                this.result = {
                    message: '',
                    status: 'success'
                };
                this.error = '';
                if (newD.includes('register')) this.getCaptcha();
            }
        },
        methods: {

            /**
             * Получение капчи
             */
            getCaptcha() {
                this.$http.get('http://127.0.0.1/get_captcha/default')
                    .then(res => {
                        this.captchaImg = res.data;
                    })
            },


            /**
             * Обработка результата запрос
             * @param {object} data
             */
            responseProcessing(data) {
                try {
                    if (data.status !== 'success')
                        throw Error('Ошибка отправки')

                    this.result = {
                        message: data.msg,
                        status: 'success'
                    }

                    setTimeout(() => {
                        this.result = {
                            message: '',
                            status: 'success'
                        }
                    }, 10000)
                } catch (e) {
                    this.error = data.msg;
                    this.result = {
                        message: '',
                        status: 'error'
                    }

                    setTimeout(() => {
                        this.result = {
                            message: '',
                            status: 'wait'
                        }
                    }, 5000)
                }
            },

            /**
             * Вход
             */
            async login() {
                //
                // /*
                //     Поверка на проход капчи
                //  */
                // if (this.reCaptchaToken === null) {
                //     this.$toast.error("reCAPTCHA не пройдена", '', {position: 'topRight'});
                //     return;
                // }

                const {email, password, reCaptchaToken} = this;
                const data = {
                    consumer_login: email,
                    password,
                    reCaptchaToken
                };
                this.error = '';
                this.result = {
                    message: '',
                    status: 'wait'
                };
                try {
                    const res = await this.$http.post('/login', data)
                    if (res.data.token) {
                        this.$store.commit('setUserInterface', {})
                        this.$bRouter.replace(res.data.default_url?res.data.default_url:'/');
                        this.$store.commit('loginUser', res.data.token);
                        res.data.use_home_page ? false : this.$store.commit('default_url', res.data.default_url);
                        this.result.status = 'success';
                    } else {
                        await this.$store.dispatch('showToast', {
                            template: 'error',
                            title: res.data.title,
                            message: res.data.msg,
                            position: res.data.position,
                        })
                        this.onCaptchaExpired();
                    }
                    this.responseProcessing(res.data);
                } catch (error) {
                    await this.$store.dispatch('showToast', {
                        template: 'error',
                        title: error.response.status,
                        message: error.response.data.message,
                        position: error.response.data.position,
                    })
                    this.onCaptchaExpired();
                }
                // const res = await this.$http.post('/login', data)
                // if (res.data.token) {
                //     this.$bRouter.replace('/');
                //     this.$store.commit('loginUser', res.data.token);
                //     this.result.status = 'success';
                // } else {
                //     this.$toast.error("Ошибка авторизации", '', {position: 'topRight'})
                // }
                // this.responseProcessing(res.data);
            },


            /**
             * Регистрация пользователя
             */
            async register() {
                const {email, password, confirmPassword, captcha, loginName, phone, firstName, lastName} = this;
                const data = {
                    consumer_login: loginName,
                    email,
                    password,
                    password_confirmation: confirmPassword,
                    captcha,
                    phone_number: phone,
                    first_name: firstName,
                    last_name: lastName
                }
                this.error = '';
                this.result = {
                    message: '',
                    status: 'wait'
                }

                const res = await this.$http.post('/consumer/register', data);
                this.responseProcessing(res.data);
            },


            /**
             * Востановление пароля
             */
            async resetPassword() {
                const {email} = this;
                const data = {
                    email
                };
                this.error = '';
                this.result = {
                    message: '',
                    status: 'wait'
                };
               try{
                   const res = await this.$http.post('/consumer/changePassword/password', data);
                   await this.$store.dispatch('showToast', {
                       template: res.data.template,
                       title: res.data.title,
                       message: res.data.message,
                       position: res.data.position,
                   })
                   this.responseProcessing(res.data);
               }
               catch(error){
                   await this.$store.dispatch('showToast', {
                       template: 'error',
                       title: error.response.status,
                       message: error.response.data.message,
                       position: error.response.data.position,
                   })
               }

            },


            /**
             * Обработчик пройденной капчи
             */
            onCaptchaVerified(reCaptchaToken) {
                this.reCaptchaToken = reCaptchaToken;
                this.login();
            },

            /**
             * Обработчик истекшей капчи
             */
            onCaptchaExpired() {
                this.$refs.recaptcha.reset()
            }
        },
        mounted() {

        },
        components: {
            VueRecaptcha
        }
    }
</script>

