                                                         viewBox="0 0 16 20">
                                                        <path id="Forma_1" data-name="Forma 1" class="cls-1"
                                                              d="M1644.74,435.4h-3.08a2.675,2.675,0,0,0-5.32,0h-3.09a2.279,2.279,0,0,0-2.26,2.284V437.8a2.3,2.3,0,0,0,1.43,2.122v10.792a2.279,2.279,0,0,0,2.26,2.285h8.63a2.273,2.273,0,0,0,2.26-2.285V439.923A2.282,2.282,0,0,0,1647,437.8v-0.118A2.272,2.272,0,0,0,1644.74,435.4Zm-5.74-1.313a1.6,1.6,0,0,1,1.57,1.313h-3.15A1.615,1.615,0,0,1,1639,434.086Zm5.5,16.629a1.2,1.2,0,0,1-1.19,1.2h-8.63a1.2,1.2,0,0,1-1.19-1.2v-10.63h11.01v10.63Zm1.43-12.914a1.2,1.2,0,0,1-1.19,1.2h-11.49a1.193,1.193,0,0,1-1.18-1.2v-0.118a1.193,1.193,0,0,1,1.18-1.2h11.49a1.2,1.2,0,0,1,1.19,1.2V437.8Zm-9.81,12.73a0.54,0.54,0,0,0,.54-0.542v-6.1a0.535,0.535,0,1,0-1.07,0v6.1A0.538,0.538,0,0,0,1636.12,450.531Zm2.88,0a0.538,0.538,0,0,0,.53-0.542v-6.1a0.535,0.535,0,1,0-1.07,0v6.1A0.54,0.54,0,0,0,1639,450.531Zm2.87,0a0.54,0.54,0,0,0,.54-0.542v-6.1a0.535,0.535,0,1,0-1.07,0v6.1A0.538,0.538,0,0,0,1641.87,450.531Z"
