<script>
    import {en, ru} from 'vuejs-datepicker/dist/locale';
    import Datepicker from 'vuejs-datepicker';
    import Multiselect from 'vue-multiselect'
    import vSelect from 'vue-select'
    import {BTable} from 'bootstrap-vue'
    import {Validator} from 'vee-validate';
    import 'jodit/build/jodit.min.css'
    import JoditVue from 'jodit-vue'
    // import 'jodit/lang/de.js'
    import axios from 'axios';
    import {saveFile} from '../mixins/saveFile'
    import {EventBus} from "../main";

    export default {
        mixins: [saveFile],
        data() {
            return {
                model_has_changed: false,
                currentOption: -1,
                selectedImg: null,
                config: {
                    fullsize: false,
                    height: 300,
                    showPlaceholder: false,
                    language: 'ru',
                    buttons: [
                        'source',
                        '|',
                        'bold',
                        'strikethrough',
                        'underline',
                        'italic',
                        // '|',
                        // 'superscript',
                        // 'subscript',
                        '|',
                        'ul',
                        'ol',
                        '|',
                        'outdent',
                        'indent',
                        '|',
                        'font',
                        'fontsize',
                        'brush',
                        'paragraph',
                        '|',
                        // 'image',
                        // 'file',
                        // 'video',
                        'table',
                        'link',
                        '|',
                        'align',
                        'undo',
                        'redo',
                        '\n',
                        'cut',
                        'hr',
                        'eraser',
                        // 'copyformat',
                        '|',
                        'symbol',
                        // 'fullsize',
                        'selectall',
                        'print',
                        'about',
                    ],
                },
                test: 'dsgdfsdfds',
                datapicker_translations: {
                    en: en,
                    ru: ru,
                },
                editorInitialized: false,
                fontFamilySelection: true

            }
        },
        computed: {},
        methods: {
            changeVSelect(item, field) {
                if (field.dependent_data && field.dependent_data.supreme) {
                    let supremeKey = field.dependent_data.supreme_field_key;
                    this.$nextTick(() => {
                        item[supremeKey] = 0;
                        let selectElement = document.querySelector('*[name="' + supremeKey + '"]');
                        selectElement.classList.add('opacitySelect');
                    });
                }

                if(field.perform_action){
                    EventBus.$emit('performActionFromZone',{
                        perform_link:field.action_link
                    })
                }

            },
            getOptionSelect(item, field) {
                if (field.dependent_data && field.dependent_data.dependent_field) {
                    return this.add_models[field.options_data.options_data_model]
                        .filter(elem => elem[field.dependent_data.dependent_field] === item[field.dependent_data.dependent_field])
                } else {
                    return this.add_models[field.options_data.options_data_model]
                }
            },

            modelChangeHandler: function () {
                this.model_has_changed = true;
            },

            dropdownClick: function (e) {
                this.currentOption = -1;
//                console.log(e.target.closest('.dropdown').getElementsByClassName('dropdown_box'))
            },

            selectSearch: function (item) {
                this.model_has_changed = true;

                /*console.log(item)*/
                if (arguments.length == 1) {
                    item.options = this.add_models[item.options_data.options_data_model].filter((i) => {
                        return i[item.options_data.options_field_title].toLowerCase().includes(item.options_data.search.toLowerCase());
                    });// Осуществляем поиск по массиву возможных опций при нажатии клавиши в поле поиска
                }
                this.currentOption = -1
            },

            chooseSelectItem: function (item, option, modelObj) {
                //item -  обьект поля, на котором произошло событие
                // option -обьект опции, которую выбрали
                //                modelObj - модель, которую будем обновлять
//                console.log('CHOOSING')
//                console.log(modelObj)
                this.model_has_changed = true;
                this.selectedImg = option.image_path;
                //console.log(item, option, modelObj,this.selectedImg);
                modelObj[item.model] = option[item.options_data.options_field_id]; // записываем id в модель
                item.options_data.search = option[item.options_data.options_field_title]; // обновляем надпись в поле поиска, чтоб пользователь видел текущую выбранную опцию
//                console.log()
            },

            //устанавливаем выделение на строку под курсором
            hoverSelectItem: function (e) {
//                console.log('elem top' + e.target.getBoundingClientRect().top);
//                console.log('block top' + e.target.parentNode.parentNode.parentNode.getBoundingClientRect().top)
                this.currentOption = e.target.parentNode.getAttribute('data-select');

            },

            dropDownKeyPress: function (e) {
                e.target.parentNode.nextSibling.nextSibling.classList.add("open");

            }, // открывает список возможных опций при нажатии клавиши в поле поиска

            dropDownArrows: function (e, item, modelObj) {
                //проверяем, есть ли элементы в списке опций
                if (e.target.parentNode.nextElementSibling.childNodes[0] && e.target.parentNode.nextElementSibling.childNodes[0].childNodes[0]) {
                    let listHeight = e.target.parentNode.nextElementSibling.childNodes[0].childNodes[0].offsetHeight;// вычисляем высоту каждой опции
                    let listQuantity = e.target.parentNode.nextElementSibling.childNodes[0].childNodes.length;// вычисляем кол-во опций
//                    console.log(e.keyCode)


                    if (e.keyCode == 40) {
                        if (this.firstSelectKeyPress) {
                            this.firstSelectKeyPress = false;
                            this.currentOption = 0;
                        } else {
                            if (this.currentOption < listQuantity - 1) this.currentOption++;
                            if (this.currentOption > 2) e.target.parentNode.nextElementSibling.scrollTop += listHeight;
                            this.previous_dr_act = 'down';

//                            console.log(e.target.parentNode.nextElementSibling.children[0].querySelector('li[data-select' + this.currentOption + ']'))
                        }
                    }

                    if (e.keyCode == 38) {
                        if (this.currentOption != 0) this.currentOption--;
                        e.target.parentNode.nextElementSibling.scrollTop -= listHeight;
                        this.previous_dr_act = 'up';
                    }

                    if (e.keyCode == 13) {
                        var elem = e.target.parentNode.nextElementSibling.childNodes[0].childNodes;
                        let index;
                        elem = Array.from(elem);
                        elem.forEach((lis) => {
                            if (lis.classList.contains('li-selected')) {
                                index = lis.getAttribute('data-select');

                            }
                        });


                        modelObj[item.model] = item.options[index][item.options_data.options_field_id];// записываем id в модель
                        item.options_data.search = item.options[index][item.options_data.options_field_title];// обновляем надпись в поле поиска, чтоб пользователь видел текущую выбранную опцию
                        e.target.parentNode.nextElementSibling.classList.remove('open') // закрываем блок с опциями
//                        console.log(e.target.parentNode.nextElementSibling)
                    }
                }
            },

            dropImgClick(e) {

                var dropdown_box = e.target.closest(".dropdown").getElementsByClassName("dropdown_box")[0];

                dropdown_box.classList.toggle('open');
                e.stopPropagation();
            },//закрытие дропдауна по клику на картинку

            checkboxChange: function (e, field) {
                this.model_has_changed = true;
//                console.log(this.relevance);
                if(field.perform_action){
                    EventBus.$emit('performActionFromZone',{
                        perform_link:field.action_link
                    })
                }
            },

            openEditor(model, name) {
                this.$store.commit('showModal', {
                    editor: {val: model[name], model, name}, type: 'editorModal', texts: {
                        save: 'save',
                        cancel: 'cancel'
                    }
                });
            },

            loadContent(from, to, e) {
                //todo  узнать, надо ли проверять эти поля валидатором
                //проверка на пустоту
                if (from == '' || to == '') {
                    this.errors.add({
                        field: 'date',
                        msg: 'Пустое поле'
                    });
                    Array.from(e.target.parentElement.getElementsByTagName('input')).forEach(input => {
                        input.classList.add('invalid');
                    })
                    return;
                } else {
                    this.errors.remove('date');
                    Array.from(e.target.parentElement.getElementsByTagName('input')).forEach(input => {
                        input.classList.remove('invalid');
                    })

                }

                var dateFrom = new Date(Date.parse(from));
                var dateTo = new Date(Date.parse(to));

                //проверка на вхождение в интервал
                if (dateFrom > dateTo) {
                    console.log('Error! date1 is < then 2')
                    console.log(this.fieldsVV);
                    console.log(this.errors)
                    this.errors.add({
                        field: 'date',
                        msg: 'Поля заполнены неверно'
                    });
                    Array.from(e.target.parentElement.getElementsByTagName('input')).forEach(input => {
                        input.classList.add('invalid');
                    })
                } else {
                    this.errors.remove('date');
                    Array.from(e.target.parentElement.getElementsByTagName('input')).forEach(input => {
                        input.classList.remove('invalid');
                    })
                }
            },

            async addFile(e, item) {
                var reader = new FileReader();

                if (e.target.files[0]) {
                    var name = e.target.files[0].name;
                    name = name.slice(0, name.lastIndexOf('.'));
                    var main_data_models = JSON.parse(JSON.stringify(item['empty_model']));
                    main_data_models[item.model_name][0]['db_area_file_name'] = name;
                    var obj = {
                        id: null,
                        storedFile: {
                            name: name,
                            extension: e.target.files[0].name.slice(e.target.files[0].name.lastIndexOf(".") + 1),
                            size: e.target.files[0].size,
                        }
                    };


                    reader.readAsDataURL(e.target.files[0]);

                    this.$validator.validate('att_file').then(res => {
                        if (res) {
                            reader.onload = async (e) => {
                                var mime = e.target.result.split(',');
                                obj['storedFile']['mime'] = mime[1];
                                obj['storedFile']['mime_type'] = mime[0];
                                main_data_models[item.model_name][0]['stored_file'] = obj;
                                this.$http.post('/admin/dbArea/file/write', {
                                    main_data_models: main_data_models,
                                    form_parameters: {form_base_data_model: item.model_name}
                                })
                                    .then(res => {
                                        this.models[item.model_name][0][item.model].push(res.data);
                                        document.getElementById('att_file').value = '';
                                    })
                                    .catch(error => {
                                        this.$store.dispatch('showToast', {
                                            template: 'error',
                                            title: error.response.status,
                                            message: error.response.data.message,
                                            position: error.response.data.position,
                                        })
                                    })


                            }
                        }
                    });
                }
            },

            att_file_action(action, model, item) {

                var data = {};
                if (action.code === 'delete_att_file') {
                    data['main_model'] = item.model_name;
                    data['items'] = model.id;
                } else {
                    data['id'] = model.id;
                    data['main_model'] = item.model_name;
                }

                this.$http.post(action.link, data)
                    .then(res => {
                        if (action.code === "download_att_file") this.saveFile(res.data);
                        if (action.code === "delete_att_file") this.deleteFile(model.id, item);
                    })
                    .catch(error => {
                        this.$store.dispatch('showToast', {
                            template: 'error',
                            title: error.response.status,
                            message: error.response.data.message,
                            position: error.response.data.position,
                        })
                    })
            },

            // saveFile(res) {
            //     var ua = window.navigator.userAgent;
            //     var msie = ua.indexOf('Trident');
            //
            //     const linkSource = res.mime + ',' + res.file;
            //     const downloadLink = document.createElement("a");
            //     const fileName = res.name + '.' + res.extension;
            //
            //     if (msie > -1) {
            //         const byteCharacters = atob(res.file);
            //         const byteNumbers = new Array(byteCharacters.length);
            //         for (let i = 0; i < byteCharacters.length; i++) {
            //             byteNumbers[i] = byteCharacters.charCodeAt(i);
            //         }
            //         const byteArray = new Uint8Array(byteNumbers);
            //
            //         var blobObject = new Blob([byteArray]);
            //
            //         window.navigator.msSaveBlob(blobObject, fileName);
            //     } else {
            //         downloadLink.href = linkSource;
            //         downloadLink.download = fileName;
            //         downloadLink.click();
            //     }
            //
            //
            // },

            deleteFile(id, item) {

                var index = this.models[item.model_name][0].files.findIndex(elem => elem.id === id);

                this.models[item.model_name][0].files.splice(index, 1);
            },

            buttonAction({action, link, model, model_name, modal}) {
                var data = {
                    id: this.models[model_name][0][model],
                    main_model: model_name
                };
                if (action !== "show_modal") {
                    this.$http.post(link, data)
                        .then(res => {
                            if (action === "download_att_file") this.saveFile(res.data);
                            if (action === "test_connection") this.testConnection(res.data);

                            this.$store.dispatch('showToast', {
                                template: res.data.template,
                                title: res.data.title,
                                message: res.data.message,
                                position: res.data.position,
                            })

                            // this.$toast[template](res.data.message, '', {position: 'topCenter'})
                        })
                        .catch(error => {

                            this.$store.dispatch('showToast', {
                                template: 'error',
                                title: error.response.status,
                                message: error.response.data.message,
                                position: error.response.data.position,
                            })
                        });
                } else {
                    // this.$store.commit('showModal',{root:this.$root, modal});
                    this.launchEditModal(link, model, model_name, modal);

                }

            },

            /**
             * приводит дату из строкового формата к  объекту Date (необходимо для корреткной работы валидации)
             * @param model - ключ поля в основной модели
             */
            transformDoubleDates({model}) {
                this.model[0][model]['from'] = new Date(this.model[0][model]['from']);
                this.model[0][model]['to'] = new Date(this.model[0][model]['to']);
            },


            testConnection(res) {
                // this.$toast.success(res.message, '', {position: 'topRight'})
            },

            multiselectChanged(selectedOption, id, model_key) {
            },

            launchEditModal(link, model, model_name, modal) {
                let self = this;


                let data = {
                    modal: {
                        ...modal,
                        model: Object.assign({}, this.models[model_name][0]),
                        addModels: this.add_data_models,
                        model_name: model_name
                    }
                }
                EventBus.$emit('showModal', data)
                EventBus.$on('triggerModelUpdate', (data) => {
                    EventBus.$emit ('performActionFromZone',{perform_link:modal.perform_after_save_link});
                    EventBus.$off('triggerModelUpdate')
                })
            },

        },
        inject: ['$validator'],
        props: [
            "fields",
            "models",
            "add_models",
            "disable_inputs",
        ],
        mounted() {


            this.imgtosvg();

            this.$ZONE = true;


        },
        updated() {
            // this.imgtosvg();
        },

        components: {
            Datepicker,
            JoditVue,
            BTable,
            Multiselect,
            vSelect
        }
    }

</script>

